import React, { forwardRef } from 'react';
import { useField, useFormikContext } from 'formik';
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";
import Styles from "./style.module.scss";

export const MyDatepicker = ({ label, ...props}) => {
  const { setFieldValue } = useFormikContext();
  const ref = React.createRef();
  const [field, meta] = useField(props);
  const CustomDateInput = forwardRef(({ value, onClick }, _ref) => (
    <span className={Styles.date} onClick={onClick} ref={_ref}>
      {value}
    </span>
  ));

  return (
    <>
      <DatePicker
        {...field}
        {...props}
        selected={field.value || new Date()}
        onChange={value => setFieldValue(props.name, value)}
        dateFormat="d MMMM yyyy"
        minDate={new Date()}
        customInput={<CustomDateInput ref={ref}/>}
      />
      {meta.touched && meta.error && <div>{meta.error}</div>}
    </>
  );
};

