import { types } from '../types';

export const manageActions = {
  addTask: (newTask) => {
    return {
      type: types.ADD_TASK,
      payload: newTask
    }
  },
  editTask: (id) => {
    return {
      type: types.EDIT_TASK,
      payload: id
    }
  },
  removeTask: (id) => {
    return {
      type: types.REMOVE_TASK,
      payload: id
    }
  },
  addNewFieldForTask: () => {
    return {
      type: types.ADD_NEW_FIELD_FOR_TASK
    }
  },
  closeFieldForTask: () => {
    return {
      type: types.CLOSE_FIELD_FOR_TASK
    }
  }
};
