import React from 'react';
import { Main } from "./components/main";
import { Footer } from './components/footer'
import { Provider } from 'react-redux';
import { store } from './init/store';

export const App = () => {
  return (
    <>
      <Provider store={store}>
        <Main />
        <Footer />
      </Provider>
    </>
  )
};
