import React, {useEffect, useRef} from 'react';
import Styles from './style.module.scss';
import { ButtonCreateTask } from "../button-create-task";
import { List } from "../list";
import { TaskCard } from "../task-card";
import { useTask } from "../../hooks/useTask";

export const Main = () => {

  const { closeFieldForTask } = useTask();

  const noneTargetElement = useRef();

  const handleClick = e => {
    if (noneTargetElement.current.contains(e.target)) {
      return;
    }
    closeFieldForTask();
  };

  useEffect(() => {
    document.addEventListener("mousedown", handleClick);
    return () => {
      document.removeEventListener("mousedown", handleClick);
    };
  });

  return (
    <main>
      <div className={Styles.controls}>
        <ButtonCreateTask />
      </div>
      <div className={Styles.wrap} onClick={handleClick} ref={noneTargetElement}>
        <List />
        <TaskCard />
      </div>
    </main>
  );
};