import { types } from '../types';

const initialState = {
  data: [],
  fieldForTask: false,
  edited: []
};

export const manageReducer = (state = initialState, { type, payload}) => {
  switch (type) {
    case types.ADD_TASK:
      return {
        ...state,
        data: [...state.data.filter((item) => item.id !== payload.id ), payload]
      };

    case types.EDIT_TASK:
      return {
        ...state,
        fieldForTask: true,
        edited: [...state.data.filter(item => item.id === payload)]
      };

    case types.REMOVE_TASK:
      return {
        fieldForTask: false,
        data: [...state.data.filter(item => item.id !== payload)],
        edited: []
      };

    case types.ADD_NEW_FIELD_FOR_TASK:
      return {
        ...state,
        fieldForTask: true,
        edited: []
      };
    case types.CLOSE_FIELD_FOR_TASK:
      return {
        ...state,
        fieldForTask: false
      };

    default:
      return state;
  }
};
