import React, { useState } from 'react';
import Styles from './style.module.scss';
import cx from 'classnames'
import { Formik, Form, FieldArray } from 'formik';
import * as Yup from 'yup';
import { useTask } from '../../hooks/useTask';
import { MyTextInput } from './myTextInput';
import { MyTextArea } from './myTextArea';
import { MyDatepicker } from './myDatepicker';
import { RadioButton } from './radioButton';

export const TaskCard = () => {

  const {edited, addTask, fieldForTask, removeTask } = useTask();
  const initialValues = edited[0] || {
    id: null,
    taskTitle: '',
    taskDescription: '',
    taskTag: '',
    taskCheckList: [],
    taskCompleted: false
  };
  const [isCompleted, toggleCompleted] = useState(initialValues.taskCompleted);
  const [counter, addCounter] = useState(543);
  const taskCardCX = cx({
    [Styles['task-card']]: true,
    [Styles.hidden]: !fieldForTask
  });
  const validationSchema = Yup.object({
    taskTitle: Yup.string().required('Required'),
    taskTag: Yup.string().required('Required')
  });

  return (
    <div className={taskCardCX}>
      <Formik
        initialValues={initialValues}
        enableReinitialize={true}
        validationSchema={validationSchema}
        onSubmit={(values, {resetForm}) => {
          addCounter(counter + 1);
          values.id = initialValues.id ? initialValues.id : counter;
          addTask(values);
          if(!initialValues.id) {
            resetForm(initialValues);
          }
        }}>
        {({values}) => {
          return (
            <FieldArray
              name="taskCheckList"
              render={(arrayHelpers) => (
                <Form>
                  <div className={Styles.head}>
                    <button className={cx({
                      [Styles['button-complete-task']]: true,
                      [Styles.completed]: arrayHelpers.form.values.taskCompleted
                    })} onClick={() => {
                      toggleCompleted(!isCompleted);
                      values.taskCompleted = !values.taskCompleted;
                    }} type='button'>Mark as complete
                    </button>
                    <button className={Styles['button-remove-task']} type='button' onClick={()=> {
                      removeTask(values.id)
                    }}/>
                  </div>
                  <div className={Styles.content}>
                    <MyTextInput type='text' name='taskTitle' id='taskTitle' label='Task title' placeholder='Task title'/>
                    <div className={Styles.deadline}>
                      <span className={Styles.label}>Due to</span>
                      <MyDatepicker name="taskDate"/>
                    </div>
                    <div className={Styles.description}>
                      <span className={Styles.label}>Description</span>
                      <MyTextArea name='taskDescription' id='taskDescription' placeholder='Describe your event' />
                    </div>
                    <div className={Styles.checklist}>
                      <span className={Styles.label}>Checklist</span>
                      <div className={Styles['sub-tasks']}>
                          {(
                            values.taskCheckList.map((item, index) => (
                              <div key={index} className={cx({
                                [Styles['sub-task']]: true,
                                [Styles['sub-task-active']]: true,
                                [Styles.completed]: item.disabled
                              })}
                              onClick={() => {
                                arrayHelpers.replace(index, {...item, disabled: !item.disabled});
                              }}>
                                {item.title}
                              </div>
                          )))}
                        <div key='taskTitle1' className={Styles['sub-task']}>
                          <MyTextInput
                            onChange={() => {
                            }}
                            onBlur={(e) => {
                              arrayHelpers.push({title: e.target.value, disabled: false});
                              e.target.value = '';
                            }}
                            type='text'
                            name='taskTitle1'
                            id='taskTitle1'
                            label='taskTitle1'
                            placeholder='Add more'
                          />
                        </div>
                      </div>
                    </div>
                    <div className={cx(Styles.tags)}>
                      <RadioButton type='radio' name='taskTag' id='radio-sketch' label='Sketch' value='Sketch' />
                      <RadioButton type='radio' name='taskTag' id='radio-spotify' label='Spotify' value='Spotify' />
                      <RadioButton type='radio' name='taskTag' id='radio-dribble' label='Dribble' value='Dribble' />
                      <RadioButton type='radio' name='taskTag' id='radio-behance' label='Behance' value='Behance' />
                      <RadioButton type='radio' name='taskTag' id='radio-ux' label='ux' value='ux' />
                    </div>
                    <div className={Styles['form-controls']}>
                      <button className={Styles['button-reset-task']} type='reset'>Reset</button>
                      <button className={Styles['button-save-task']} type='submit'>Save</button>
                    </div>
                  </div>
                </Form>
              )}
            />
          )
        }}
      </Formik>
    </div>
  );
};