import { useSelector, useDispatch } from 'react-redux';

import { manageActions } from '../redux/actions/manager';

export const useTask = () => {
  const selector = state => state.task;
  const { data, fieldForTask, edited } = useSelector(selector);
  const dispatch = useDispatch();

  const addTask = _task => {
    dispatch(manageActions.addTask(_task));
  };

  const addNewFieldForTask = () => {
    dispatch(manageActions.addNewFieldForTask());
  };

  const closeFieldForTask = () => {
    dispatch(manageActions.closeFieldForTask());
  };

  const editTask = _id => {
    dispatch(manageActions.editTask(_id));
  };

  const removeTask = _id => {
    dispatch(manageActions.removeTask(_id));
  };

  return {
    fieldForTask,
    data,
    edited,
    addTask,
    editTask,
    removeTask,
    addNewFieldForTask,
    closeFieldForTask
  };
};