import React from 'react';
import Styles from './style.module.scss';
import { Tag } from "../tag";
import { useTask } from '../../hooks/useTask';
import cx from 'classnames'
import moment from "moment";

export const List = () => {

  const { data, editTask } = useTask();

  const tasks = data.map(({taskTitle, taskDate, taskTag, taskCompleted, id}) => (
    <div className={cx({[Styles.task]: true, [Styles.completed]: taskCompleted })} key={id} id={id} onClick={() => {
      editTask(id);
    }}>
      <span className={Styles.title}>{taskTitle}</span>
      <div className={Styles.meta}>
        <span className={Styles.deadline}>{moment(taskDate).format('DD/MM/YYYY')}</span>
        <Tag title={taskTag} />
      </div>
    </div>
  ));

  const listCX = cx({
    [Styles.list]: true,
    [Styles.empty]: !data.length
  });

  return (
    <div className={ listCX }>
      <div className={Styles.tasks}>
        {tasks}
      </div>
    </div>
  );
};